<!--
 * @Descripttion: 流量
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-24 14:33:02
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-09-02 12:09:45
-->
<template>
    <div class="serviceinfo-content">
        <div class="serviceinfo-title">
            <div class="left">
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">IO</p>
            </div>
            <div class="right">
                <el-select v-model="active_disk" size="mini" placeholder="请选择硬盘" @change="changeDisk">
                    <el-option
                    v-for="item in disk"
                    :key="item"
                    :label="item"
                    :value="item">
                    </el-option>
                </el-select>
                 <el-date-picker
                    v-model="time"
                    type="datetimerange"
                    range-separator="~"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    align="right"
                    size="mini"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    @change="changeTime"
                    >
                </el-date-picker>
            </div>
        </div>
        <div class="service-line">
             <div class="line-info">
                <div class="line">
                    <!-- 折线图 -->
                    <div class="" id="diskio" :style="{ height: '360px','margin-bottom':'20px'}"></div>
                </div>
            </div>
        </div>
        <el-divider></el-divider>
    </div>
</template>
<script>
    export default{
        props:{
            hostId:{
                default:'',
            }
        },
        data(){
            return{
                resData:null,
                echarts:null,
                time:'',
                option:null,
                startTime:'',
                endTime:'',
                disk:[],
                active_disk:''
            }
        },
        methods:{
           initEchats(){
                this.option = {
                    xAxis: {
                        type: 'category',
                        data: this.xTitle,
                        axisPointer: {
                            lineStyle: {
                                color: '#d35454', //提示线颜色
                                width: 2
                            },
                        },
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: (value, index)=> {
                                var size = this.getSize(value, false);
                                return size.int + " " + size.format;
                            }
                        }
                    },
                    legend:{
                        data:['读取速度','写入速度']
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {},
                            restore:{}
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        formatter:(params)=>{
                            var inSize = this.getSize(params[0].data, false);
                            var outSize = this.getSize(params[1].data, false);
                            return `${params[0].name}<br/>${params[0].marker}${params[0].seriesName}: ${inSize.float} ${inSize.format}/s<br/>${params[1].marker}${params[1].seriesName}: ${outSize.float} ${outSize.format} /s`
                        }
                    },
                    dataZoom: [
                        {
                            type: 'inside',
                        },
                    ],
                    grid:{
                        x:50, //左侧未知
                        x2:0 //右侧
                    },

                    series: [
                        {
                            name:'读取速度',
                            data: this.xData1,
                            type: 'line',
                            smooth: true,
                            itemStyle:{
                                color:'#d35454' //线的颜色
                            },
                            areaStyle:{
                                normal:{
                                    color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                        {
                                            offset: 0,
                                            color: 'rgba(211,84,84,0.5)' //渐变上
                                        },
                                        {
                                            offset: 1,
                                            color: 'rgba(211,84,84,0)' //渐变下
                                        }
                                    ]
                                )}
                            }
                        },
                        {
                            name:'写入速度',
                            data: this.xData2,
                            type: 'line',
                            smooth: true,
                            itemStyle:{
                                color:'rgba(72,142,237,1)' //线的颜色
                            },
                            areaStyle:{
                                normal:{
                                    color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                        {
                                            offset: 0,
                                            color: 'rgba(72,142,237,0.5)' //渐变上
                                        },
                                        {
                                            offset: 1,
                                            color: 'rgba(72,142,237,0)' //渐变下
                                        }
                                    ]
                                )}
                            }
                        }
                    ]
                };
                this.echarts = this.$echarts.init(document.getElementById('diskio'));
                this.echarts.setOption(this.option);
            },
             getHostInfoEchart(){
                var params = {
                    hostId:this.hostId,
                    type:6,
                    dateType:1,
                    startTime:this.startTime,
                    endTime:this.endTime
                }
                capis.getHostInfoEchart(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //获取到数据，
                        for(var key in data.res.data)
                        {
                            this.disk.push(key);
                        }
                        this.active_disk = this.disk[0];
                        this.resData = data.res;

                        //设置x坐标
                        this.xTitle = data.res.header.reverse();
                        this.echarts.setOption({
                            xAxis:{data:this.xTitle},
                            legend:{data:data.res.y}
                        });
                        //判断是否已经有存在的激活的硬盘
                        if(this.active_disk && this.resData['data'][this.active_disk])
                        {
                             this.echarts.setOption({
                                series:[{
                                    data: this.resData['data'][this.active_disk][0]
                                },
                                {
                                    data: this.resData['data'][this.active_disk][1]
                                }],
                            });
                        }
                        
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            changeTime(time){
                this.startTime = time[0];
                this.endTime = time[1];
                this.getHostInfoEchart();
            },
            changeDisk (){
                if(this.active_disk && this.resData['data'][this.active_disk])
                {
                    this.echarts.setOption({
                        series:[{
                            data: this.resData['data'][this.active_disk][0]
                        },
                        {
                            data: this.resData['data'][this.active_disk][1]
                        }],
                    });
                }
            },
            getSize(size,byte=false){
                var data = {"int":size,"float":size,"format":byte?"byte":"KB"};
                var sizeInt = Math.ceil(size);
                if(byte) {
                    if(sizeInt>1099511627776)
                    {
                        var sizeTmp = (sizeInt / 1099511627776 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"TB"};
                    }
                    else if(sizeInt>1073741824)
                    {
                        var sizeTmp = (sizeInt / 1073741824 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"GB"};
                    }
                    else if(sizeInt>1048576)
                    {
                        var sizeTmp = (sizeInt / 1048576 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"MB"};
                    }
                    else if(sizeInt>1024)
                    {
                        var sizeTmp = (sizeInt / 1024 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"KB"};
                    }
                    else
                    {
                        data = {"int":size,"float":size,"format":"byte"};
                    }
                }
                else {
                    if(sizeInt>1073741824)
                    {
                        var sizeTmp = (sizeInt / 1073741824 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"TB"};
                    }
                    else if(sizeInt>1048576)
                    {
                        var sizeTmp = (sizeInt / 1048576 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"GB"};
                    }
                    else if(sizeInt>1024)
                    {
                        var sizeTmp = (sizeInt / 1024 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"MB"};
                    }
                    else
                    {
                        data = {"int":size,"float":size,"format":"KB"};
                    }
                }
                return data;
            },
            initWs(){
                this.$LaravelEcho.instance.channel('SysInfo').listen('Snmp.SysInfoEvent', (e) => {
                    //检测io的使用率
                    if(e.type=="DiskIoInfo")
                    {
                        if(this.hostId==e.host_id)
                        {
                            //循环数据
                            var json = e.data;
                            if(json && json.length>0)
                            {
                                for(var i=0; i<json.length; i++)
                                {
                                    if(this.resData["data"][json[i]["disk_name"]])
                                    {
                                        this.resData["data"][json[i]["disk_name"]][0].push(json[i]["kB_read_avg"]["size"])
                                        this.resData["data"][json[i]["disk_name"]][1].push(json[i]["kB_wrtn_avg"]["size"])
                                    }
                                }
                                this.resData["header"].push(ctool.getYMDHMS(e.time))
                            }
                            if(this.active_disk && this.resData["data"][this.active_disk])
                            {
                                this.echarts.setOption({
                                series: [
                                    {
                                        data: this.resData["data"][this.active_disk][0]
                                    },
                                    {
                                        data: this.resData["data"][this.active_disk][1]
                                    }
                                ],
                                xAxis:{data:this.resData["header"]}
                            });
                            }
                            
                        }
                    }
                })
            }
        },
        components:{
        },
        created(){
            this.getHostInfoEchart();
            this.$nextTick(()=>{
                this.initEchats();
                this.initWs();
            })
        },
    }
</script>

<style scoped lang="scss">
    
    .serviceinfo-content{
        width: 50%;
        .serviceinfo-title{
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .left{
                display: flex;
            }
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
       
</style>


