<!--
 * @Descripttion: 系统日志
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:31
-->
<template>
    <div>
        <div>
            <el-row class="tac">
                <el-col :span="4" style="margin-top: 20px;">
                    <el-menu :default-active="activeIndex" mode="vertical" style="height: 100" active-text-color="#5e5ec6" v-for="(it,ind) in list" :key="it">
                        <el-menu-item @click="ClickLog(it)" :index="ind+it" >{{it}}</el-menu-item>
                    </el-menu>
                </el-col>
                <el-col :span="19" class="tac-item">
                    <textarea v-model="fileInfo" readonly style="margin-top: 20px;margin-left: -15px;width: 102.5%;height: 64.5vh;background-color: #333;color:#fff; padding:0 5px"></textarea>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                list:[],
                activeIndex: '',
                hostId:'',
                type:'',
                fileInfo:''
            }
        },
        methods: {
            hostseverLogDir(){
               var params = {
                server:this.type
               }
                capis.hostseverLogDir(this.hostId,params,{})
                .then(data=>{
                    console.log(72,this.list)
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            ClickLog(fileName){
                var params = {
                    server:this.type,
                    fileName:fileName
                }
                capis.hostseverLog(this.hostId,params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.fileInfo = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            }
        },
        created(){
            this.hostId = this.$route.query.hostId;
            this.type = this.$route.query.type;
            this.hostseverLogDir();
        },
        components:{
        },
    }
</script>

<style scoped lang="scss">
.tac{
    text-align:center; /* 文字居中 */
    .tac-item{
        float:right;
        margin-right: 30px;
    }
}
</style>


