-->
<template>
    <div class="warning-add">
        <p class="warning-add-title">基础选项</p>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
            <el-form-item label="源 地 址：" prop="source">
                 <el-input v-model="form.source" clearable></el-input>
             </el-form-item>
             <el-form-item label="端　　口：" prop="ports">
                 <el-input v-model="form.ports" clearable></el-input>
             </el-form-item>
             <el-form-item label="协　　议：" prop="protocol">
                  <el-select v-model="form.protocol" placeholder="请选择" clearable>
                     <el-option
                         label=" tcp"
                         value="1">
                     </el-option>
                     <el-option
                         label=" udp"
                         value="2">
                     </el-option>
                 </el-select>
             </el-form-item>
             <el-form-item label="类　　型：" prop="types">
                  <el-select v-model="form.types" placeholder="请选择" clearable>
                     <el-option
                         label=" 放行 "
                         value="1">
                     </el-option>
                     <el-option
                         label=" 禁止 "
                         value="2">
                     </el-option>
                 </el-select>
             </el-form-item>
             <el-form-item label="备　　注：" prop="brief">
                 <el-input v-model="form.brief" clearable></el-input>
             </el-form-item>
             <el-form-item>
                 <el-button type="primary" @click="submitForm('form')" :loading="formLoading">增加</el-button>
                 <el-button @click="cancel">取消</el-button>
             </el-form-item>
         </el-form>
    </div>
 </template>
 <script>
     export default{
        props:{
            hostId:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    source:'',
                    ports:'',
                    protocol:'',
                    types:'',
                    brief:'', 
                },
                rules: {
                    source: [
                        { required: true, message: '请输入源地址', trigger: 'blur' },
                    ],
                    ports: [
                        { required: true, message: '请输入端口', trigger: 'blur' },
                    ],
                    protocol: [
                        { required: true, message: '请选择协议', trigger: 'blur' },
                    ],
                    types: [
                        { required: true, message: '请选择类型', trigger: 'blur' },
                    ],
                },
                roleList:[], // 服务器角色的数据
                formLoading:false,
                NotificationSettingIdArr:null, //告警规则
            }
        },
        components:{
        },
        created(){
        },
         methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.formLoading=true;
                        //请求接口
                        var params = {
                        uri:'/iptables/create_rules',
                        source:this.form.source,
                        ports:this.form.ports,
                        protocol:this.form.protocol,
                        types:this.form.types,
                        brief:this.form.brief,
                        hostId:this.hostId
                        }
                        capis.hostseverpyinfo(params,{})
                        .then(data=>{
                            this.formLoading = false;
                            if(data.status == 'success'){
                                //对数据进行赋值
                                // 修改父组件的值
                                this.$emit('freshData',{
                                    type:'add',
                                })
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                    .catch(err=>{
                        this.formLoading = false;
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                            });
                        })
                     
                    }
                });
            },
            cancel(){
               this.$emit('freshData',{
                    type:'cancel',
                })
            },
        }
    }
 </script>
 <style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
        .warning-add-title{
            font-size: 28px;
            font-weight: 600;
            height: 60px;
            line-height: 60px;
        }
        .el-select{
            display: block;
        }
    }
 </style>


