<!--
 * @Descripttion: 设备详情
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-09-02 12:29:30
-->
<template>
    <div class="info-box">
        <div class="top-tilte">
            <div class="serviceinfo-title">
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">设备信息</p>
            </div>
            <div class="service-fresh-time" v-if="hostInfo && hostInfo.updated_at">
                更新时间:{{hostInfo.updated_at}}
            </div>
        </div>
        <div class="service-item-content" v-if="hostInfo">
            <div class="item">
                <p class="item-title"><i class="el-icon-s-operation"></i><span>设备名称</span></p>
                <p class="item-name">{{hostInfo.name}}</p>
            </div>
             <div class="item">
                <p class="item-title"><i class="el-icon-c-scale-to-original"></i><span>设备类型</span></p>
                <p class="item-name">{{hostInfo.type==1?'centos服务器':''}} </p>
            </div>
             <div class="item">
                <p class="item-title"><i class="el-icon-location-outline"></i><span>IP地址</span></p>
                <p class="item-name">{{hostInfo.ip}}</p>
            </div>
             <div class="item">
                <p class="item-title"><i class="el-icon-monitor"></i><span>操作系统</span></p>
                <p class="item-name">{{hostInfo.systemVersion}}</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-data-line"></i><span>设备状态</span></p>
                <p class="item-name">{{hostInfo.running=='1'?'在线':'离线'}}</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-cpu"></i><span>CPU</span></p>
                <p class="item-name">{{hostInfo.cpuInfo}}-({{hostInfo.cpuCoreNum}}核)</p>
            </div>
            <div class="item" v-if="hostInfo.memoryUse && hostInfo.memoryTotal">
                <p class="item-title"><i class="el-icon-bank-card"></i><span>内存</span></p>
                <p class="item-name">
                    <span>
                        {{hostInfo.memoryUse.size}}{{hostInfo.memoryUse.format}}
                    </span>
                    <span>
                        {{hostInfo.memoryTotal.size}}{{hostInfo.memoryTotal.format}}(已使用{{hostInfo.memUsedPercent}}%)
                    </span>
                </p>
            </div>
             <div class="item" v-if="hostInfo.netSpeed">
                <p class="item-title"><i class="el-icon-bank-card"></i><span>网络</span></p>
                <p class="item-name">
                    <span>
                        {{hostInfo.netSpeed.in.size}}{{hostInfo.netSpeed.in.format}}
                    </span>
                    <span>
                        {{hostInfo.netSpeed.out.size}}{{hostInfo.netSpeed.out.format}}
                    </span>
                </p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-timer"></i><span>告警数</span></p>
                <p class="item-name">
                    <span>
                        今日：2
                    </span>
                    <span>
                        近七天：4
                    </span>
                </p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-connection"></i><span>SSH</span></p>
                <p class="item-name">
                    <span>
                        成功：{{hostInfo.toDayLoginSucceed}}
                    </span>
                    <span>
                        失败：{{hostInfo.toDayLoginFail}}
                    </span>
                 </p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-connection"></i><span>启用端口</span></p>
                <p class="item-name open-port">
                 <span @click="dialogShow('tcp')">
                    TCP：<em v-if="hostInfo.tcpPort">{{hostInfo.tcpPort.length}}</em>
                 </span>
                 <span @click="dialogShow('udp')">
                    UDP：<em v-if="hostInfo.udpPort">{{hostInfo.udpPort.length}}</em>
                 </span> 
                </p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-connection"></i><span>运行服务数</span></p>
                <p class="item-name">
                    <span @click="dialogOpen">
                        <em v-if="hostInfo.runningServers">{{hostInfo.runningServers.length}}</em>
                    </span>
                </p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-time"></i><span>系统时间</span></p>
                <p class="item-name">{{hostInfo.systemTime}}</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-tickets"></i><span>设备运行天数</span></p>
                <p class="item-name">{{hostInfo.systemRuntime}}</p>
            </div>
        </div>
        <div class="device-other-services" v-if="hostInfo">
            <!-- 使用率-负载 -->
            <div class="fuzai" style="border-right:1px solid #eee;">
                <!-- 使用率 -->
                <div class="fuzaicontent">
                    <div class="box" v-if="hostInfo.cpuUse">
                        <el-tooltip class="item" effect="dark" :content="hostInfo.cpuCoreNum+'核'" placement="top">
                            <p class="process-name">CPU使用率</p>
                        </el-tooltip>
                        <el-progress type="circle" :percentage="parseFloat(hostInfo.cpuUse)" :width="80" :stroke-width="13"
                        :color="getProgressColor(hostInfo.cpuUse)"></el-progress>
                    </div>
                    <div class="box" v-if="hostInfo.memoryUse">
                        <el-tooltip class="item" effect="dark" :content="hostInfo.memoryUse.size+hostInfo.memoryUse.format+'/'+hostInfo.memoryTotal.size+hostInfo.memoryTotal.format" placement="top">
                            <p class="process-name">内存使用率</p>
                        </el-tooltip>
                        <el-progress type="circle" :percentage="parseFloat(hostInfo.memUsedPercent)" :width="80" :stroke-width="13" :color="getProgressColor(hostInfo.memUsedPercent)"></el-progress>
                    </div>
                    <!-- <div class="box">
                        <p class="process-name">负载率</p>
                        <el-progress type="circle" :percentage="25" :width="80" color="#5ec6ac" :stroke-width="13"></el-progress>
                    </div> 
                    <div class="box">
                        <p class="process-name">温度</p>
                        <el-progress type="circle" :percentage="25" :width="80" color="#e05a25" :stroke-width="13"></el-progress>
                    </div>  -->
                </div>
               
            </div>
             <div class="fuzai">
                <!-- 负载 -->
                <div class="fuzai3">
                    <div class="right">
                        <i class="el-icon-set-up"></i>
                        <span>负载</span>
                    </div>
                    <div class="fuzai-1-3-5">
                        <el-tooltip class="item" effect="dark" content="1分钟负载" placement="top">
                            <div class="fuzaiinfo" style="border-right:1px solid #eee;">{{hostInfo.cpuLoad1}}</div>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="5分钟负载" placement="top">
                            <div class="fuzaiinfo" style="border-right:1px solid #eee;">{{hostInfo.cpuLoad5}}</div>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="15分钟负载" placement="top">
                            <div class="fuzaiinfo">{{hostInfo.cpuLoad15}}</div>
                        </el-tooltip>
                    </div>
                </div>
            </div>
        </div>
        <el-divider></el-divider>
        <div class="device-other-services" v-if="hostInfo">
            <!-- 硬盘 -->
            <div class="fuzai"  style="border-right:1px solid #eee;">
                 <div class="disk" v-for="item in hostInfo.diskInfo" :key="item.disk_name">
                  <div class="disk-title">
                      <i class="el-icon-film"></i>
                      <span>硬盘：{{item.disk_name}}  占用率：({{item.disk_indoes_used_percent}})</span>
                  </div>
                  <div class="disk-process" v-if="item.disk_used_percent">
                        <el-progress  status="success"  :text-inside="true" :stroke-width="20" :percentage="parseFloat(item.disk_used_percent)" :color="getProgressColor(item.disk_used_percent)"></el-progress>
                  </div>
                  <div class="info">
                        <div class="disk-info">
                                <span>
                                    {{item.disk_used_size.size}}{{item.disk_used_size.format}} 已使用
                                </span>
                                <span>
                                    {{item.disk_size.size}}{{item.disk_size.format}} 总量
                                </span> 
                        </div>
                        <div class="disk-io-info">
                                <span>
                                    读取速度 {{item.kB_read_avg.size}} / {{item.kB_read_avg.format}}
                                </span>
                                <span>
                                    写入速度 {{item.kB_wrtn_avg.size}} / {{item.kB_wrtn_avg.format}}
                                </span>
                                <span>
                                    %util {{item.util}}
                                </span> 
                        </div>
                    </div>
               </div>
               
            </div>
            <div class="fuzai">
                <!--  挂载点-->
               <div class="disk" v-for="item in hostInfo.storageInfo" :key="item.mounted">
                  <div class="disk-title">
                      <i class="el-icon-film"></i>
                      <span>文件系统：{{item.partition}} | 挂载点：{{item.mounted}}  </span>
                  </div>
                  <div class="disk-process">
                        <el-progress  :text-inside="true" :stroke-width="20" :percentage="parseFloat(item.used_percent)" :color="getProgressColor(item.used_percent)"></el-progress>
                  </div>
                  <div class="disk-info">
                      已使用：{{item.used.size}}{{item.used.format}} / 总量：{{item.total.size}}{{item.total.format}}
                  </div>
               </div>
            </div>
             
        </div>
        <el-divider></el-divider>
        <!-- 端口表格 -->
        <el-dialog
            :title="dialogModel+'端口'"
            :visible.sync="dialogVisible"
            width="60%">
            <el-table
                :v-if="dialogModel=='tcp'||dialogModel=='udp'"
                :data="dialogModel=='tcp'?hostInfo.tcpPort:hostInfo.udpPort"
                :row-style="{height: '0'}"
                :cell-style="{padding: '5px'}"
                height="45vh"
                style="width: 100%"
                :header-cell-style="{background:'#f7f7fc'}"
                :border="true">
                <el-table-column
                prop="port"
                label="端口"
                width="180">
                </el-table-column>
                <el-table-column
                prop="recv_q"
                label="端口接收队列"
                width="180">
                </el-table-column>
                <el-table-column
                prop="send_q"
                label="端口发送队列">
                </el-table-column>
                <el-table-column
                prop="name"
                label="端口服务">
                </el-table-column>
            </el-table>
        </el-dialog>
        <!-- 服务表格 -->
        <el-dialog
            v-if="runningServers"
            :title="'运行服务'"
            :visible.sync="runningServers"
            width="60%">
            <el-table
                :v-if="hostInfo.runningServers.length>0"
                :data="hostInfo.runningServers"
                :row-style="{height: '0'}"
                :cell-style="{padding: '5px'}"
                height="45vh"
                :header-cell-style="{background:'#f7f7fc'}"
                :border="true"
                style="width: 100%"
                >
                <el-table-column
                prop="name"
                label="服务名称"
                width="240">
                </el-table-column>
                <el-table-column
                prop="pid"
                label="进程ID"
                width="180">
                </el-table-column>
                <el-table-column
                prop="ppid"
                label="父进程ID">
                </el-table-column>
                <el-table-column
                prop="status"
                label="服务状态">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.status=='sleeping'" type="warning" size="small">休眠中</el-tag>
                        <el-tag v-else-if="scope.row.status=='running'" type="success" size="small">运行中</el-tag>
                        <el-tag v-else-if="scope.row.status=='stopped'" type="danger" size="small">停止中</el-tag>
                        <el-tag v-else-if="scope.row.status=='zombie'" type="info" size="small">僵尸状态</el-tag>
                        <el-tag v-else-if="scope.row.status=='dead'" type="danger" size="small">死亡状态</el-tag>
                        <el-tag v-else-if="scope.row.status=='Disk sleep'" type="danger" size="small">磁盘休眠中</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </el-dialog> 
    </div>
</template>

<script>
    export default{
        props:{
            hostId:{
                default:''
            },
            // runningServers:''
        },
        data(){
            return{
              hostInfo:{},
              dialogVisible: false,
              dialogModel: '',
              dialogServers:'',
              runningServers: false,
              progressColors:{
                    'success': '#67C23A',
                    'warning': '#E6A23C',
                    'danger': '#F56C6C',
                }
            }
        },
        methods:{
            getHostInfoEnable(){
                var params = {
                    hostId:this.hostId
                }
                    capis.getHostInfoEnable(params,{})
                    .then(data=>{
                        this.tableLoading = false;
                        if(data.status == 'success'){
                            //对数据进行赋值
                            let hostInfo = data.res.data;
                            let diskInfo = hostInfo.diskInfo;
                            let diskIo = hostInfo.diskIo;
                            for (let i=0; i<diskInfo.length; i++){
                                for (let j=0; j<diskIo.length; j++){
                                    if(diskInfo[i].disk_name.indexOf(diskIo[j].disk_name) != -1)
                                    {
                                        diskInfo[i].kB_read_avg = diskIo[j].kB_read_avg;
                                        diskInfo[i].kB_wrtn_avg = diskIo[j].kB_wrtn_avg;
                                        diskInfo[i].util = diskIo[j].util;
                                        break;
                                    }
                                }
                            }
                            hostInfo.diskInfo = diskInfo;
                            this.hostInfo = hostInfo;
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.tableLoading = false;
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('Snmp.HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.running = e.data.running;
                        }
                    }
                })
                this.$LaravelEcho.instance.channel('SysInfo').listen('Snmp.SysInfoEvent', (e) => {
                    //检测cpu的使用率
                    if(e.type=="CpuInfo")
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.cpuUse = parseFloat(e.data.cpuUse);
                            this.hostInfo.cpuLoad1 = e.data.cpuLoad1;
                            this.hostInfo.cpuLoad5 = e.data.cpuLoad5;
                            this.hostInfo.cpuLoad15 = e.data.cpuLoad15;
                        }
                    }
                    //内存的使用率
                    else if(e.type =='MemoryInfo')
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.memUsedPercent = parseFloat(e.data.memUsedPercent);
                        }
                    }
                    //网络
                    else if(e.type =='ifInfo')
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.netSpeed.in.size = e.data.escape.in.size; //下行量
                            this.hostInfo.netSpeed.in.format = e.data.escape.in.format; //下行单位
                            this.hostInfo.netSpeed.out.size = e.data.escape.out.size; //上行量
                            this.hostInfo.netSpeed.out.format = e.data.escape.out.format; //上行单位
                        }
                    }
                    //基本信息
                    else if(e.type == 'BaseInfo')
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.systemRuntime = e.data.systemRuntime; //系统运行事件
                            this.hostInfo.systemTime = e.data.systemTime; //系统时间
                            this.hostInfo.runningServers = e.data.hrswrunname; //运行的服务
                        }
                    }
                    //挂载点
                    else if(e.type == 'StorageInfo')
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.storageInfo = e.data; //挂载点
                        }
                    }
                    //硬盘
                    else if(e.type == 'DiskInfo')
                    {
                        let diskInfo = this.hostInfo.diskInfo;
                        if(this.hostId==e.host_id)
                        {
                            // this.hostInfo.diskInfo = e.data; //挂载点
                            for(var i=0; i< e.data.length; i++)
                            {
                                for(var j=0; j< diskInfo.length; j++)
                                {
                                    if(diskInfo[j].disk_name == e.data[i].disk_name)
                                    {
                                        diskInfo[j] = {...e.data[i],...diskInfo[j]};
                                        break;
                                    }
                                }
                            }
                        }
                    }
                    //硬盘IO
                    else if(e.type == 'DiskIoInfo')
                    {
                        let diskIo = e.data;
                        let diskInfo = this.hostInfo.diskInfo;
                        for (let i=0; i<diskInfo.length; i++){
                            for (let j=0; j<diskIo.length; j++){
                                if(diskInfo[i].disk_name.indexOf(diskIo[j].disk_name) != -1)
                                {
                                    diskInfo[i].kB_read_avg = diskIo[j].kB_read_avg;
                                    diskInfo[i].kB_wrtn_avg = diskIo[j].kB_wrtn_avg;
                                    diskInfo[i].util = diskIo[j].util;
                                    break;
                                }
                            }
                        }
                    }
                    //端口
                    else if(e.type == 'OpenPortInfo')
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.hostInfo.tcpPort = e.data.tcp; //tcp端口
                            this.hostInfo.udpPort = e.data.udp; //udp端口
                        }
                    }
                })
            },
            //进度条颜色
            getProgressColor(percentage) {
                if(percentage <= 70) {
                    return this.progressColors.success
                }
                else if(percentage > 70 && percentage <= 90) {
                    return this.progressColors.warning
                }
                else{
                    return this.progressColors.danger
                }
            },
            dialogShow(model){
                this.dialogVisible=true;
                this.dialogModel=model;
            },
            dialogOpen(){
                this.runningServers=true;
                // this.dialogServers=servers;
            }
        },
        created(){
            this.initWs();
            this.getHostInfoEnable();
        },
    }
</script>

<style scoped lang="scss">
   .info-box{
        .top-tilte{
            display: flex;
            justify-content: space-between;
            .serviceinfo-title{
                display: flex;
                font-size: 24px;
                font-weight: 600;
                .serviceinfo-back{
                    i{
                        font-size: 30px;
                        color: #ceceee;
                    }
                }
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                }
                .serviceinfo-tit{
                    margin-left: 20px;
                }
            }
            .service-fresh-time{
                font-size: 14px;
                color: #999;
            }
            
        }
       .service-item-content{
           padding: 22px;
           display: flex;
           flex-wrap: wrap;
            .item{
                box-sizing: border-box;
                padding: 22px;
                width: 20%;
                
                .item-title{
                    span{
                        font-size: 18px;
                        color: #333;
                        font-weight: bold;
                    }
                    i{
                        font-size: 18px;
                        color: #5e5ec6;
                        margin-right: 10px;
                    }
                }
                .item-name{
                    font-size: 12px;
                    color: #999;
                    font-weight: 500;
                    margin-top: 15px;
                }
                .item-name span{
                    &:not(:last-child)::after {
                        padding: 0 2px; color: #ddd;content: "/";
                    }
                }
                .item-name em{
                    font-style:normal;
                }
                .item-name em:hover{
                    color: #5e5ec6;
                    border-bottom: 1px solid ;
                    padding-bottom: 2px
                }
            }
        }
   }
   .device-other-services{
       display: flex;
       justify-content: space-between;
       .fuzai{
           box-sizing: border-box;
        //    padding: 44px;
           width: 50%;
           .fuzaicontent{
               display: flex;
               .box{
                   width: 25%;
                   display: flex;
                   flex-direction: column;
                   align-items: center;
                   .process-name{
                       font-size: 14px;
                    //    color: #5e5ec6;
                    font-weight: 600;
                       text-align: center;
                       height: 30px;
                       line-height: 30px;
                   }
               }
           }
           .fuzai3{
               .right{
                   margin-left: 20px;
                   i{
                       font-size: 24px;
                       color: #5e5ec6;
                   }
                   span{
                       font-size: 22px;
                   }
                   padding-bottom: 30px;
               }
               .fuzai-1-3-5{
                   display: flex;
                   box-sizing: border-box;
                   
                   .fuzaiinfo{
                       width: 33%;
                       font-size: 36px;
                       color: #5e5ec6;
                    //    padding: 30px 0;
                       text-align: center;
                   }
               }
           }
       }
   }
   .disk{
       padding: 10px 20px;
       .disk-title{
           i{
               font-size: 14px;
               color: #5e5ec6;
               margin-right:10px ;
           }
            span{
                font-size: 14px;
            }           

       }
       .disk-process{
           padding: 10px 40px 10px 0;
       }
       .info {
           display: flex;
           justify-content: space-between;
       }
       .disk-info{
           font-size: 12px;
           color: #999;
       }
       .disk-io-info{
           font-size: 12px;
           color: #999;
           padding-right: 40px;
       }
       .disk-info span { 
            &:not(:last-child)::after {
                padding: 0 5px; color: #ddd;content: "|";
            }
        }
       .disk-io-info span { 
            &:not(:last-child)::after {
                padding: 0 5px; color: #ddd;content: "|";
            }
        }
   }  
</style>


