-->
<template>
    <div >
        <div class="" id="cpuusenumber" :style="{ height: '360px','margin-bottom':'20px'}"></div>
    </div>
 </template>
 <script>
     export default{
         data(){
             return{
               hostId:'',
               type:''
             }
         },
         methods:{
            initEchats(){
                 var option = {
                     xAxis: {
                         type: 'category',
                         data: ['3-21', '3-22', '3-23', '3-24', '3-25', '3-26', '3-27'],
                         axisPointer: {
                             lineStyle: {
                                 color: '#5e5ec6', //提示线颜色
                                 width: 2
                             },
                         },
                     },
                     yAxis: {
                         type: 'value'
                     },
                     tooltip: {
                         trigger: 'axis'
                     },
                     toolbox: {
                         feature: {
                         saveAsImage: {}
                         }
                     },
                     grid:{
                         x:50, //左侧未知
                         // y:0
                         x2:0 //右侧
                     },
 
                     series: [
                         {
                         data: [0, 932, 901, 934, 1290, 1330, 0],
                         type: 'line',
                         smooth: true,
                         itemStyle:{
                             color:'#5e5ec6' //线的颜色
                         },
                         areaStyle:{
                             normal:{
                                 color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                     {
                                         offset: 0,
                                         color: 'rgba(93,115,199,0.5)' //渐变上
                                     },
                                     {
                                         offset: 1,
                                         color: 'rgba(93,115,199,0)' //渐变下
                                     }
                                 ]
                             )}
                             }
                         }
                     ]
                 };
                 this.echarts = this.$echarts.init(document.getElementById('cpuusenumber'));
                 this.echarts.setOption(option);
             }
         },
          mounted(){
             this.initEchats();
         },
         components:{
         },
         computed:{
             
         },
         created(){
            this.hostId = this.$route.query.hostId;
            this.type = this.$route.query.type;
        },
     }
 </script>
 
 <style scoped lang="scss">
    
 </style>


