<!--
 * @Descripttion: OID管理
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 22:42:04
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:12:48
-->
<template>
    <div class="devices">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="OidAdd = !OidAdd">新增</el-button>
                </el-form-item>
            </el-form>
            
        </div>
        <div class="message-table">
            <el-table
                ref="record-table"
                :data="list"
                style="width: 100%"
                stripe
                :height = "tableHeight"
                highlight-current-row
                v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="服务名称"
                prop="serverName"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="监控名称"
                prop="serverType"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="Oid"
                prop="oid"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="类型"
                prop="type"
                show-overflow-tooltip
                >
                    <template slot-scope="scope">
                        <div class="dedevice-run">
                            <p v-if="scope.row.type == 1">系统自带</p>
                            <p v-if="!scope.row.type == 1">用户安装</p>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                label="状态"
                >
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value='1'
                            :inactive-value='2'
                            @change="changeStatus(scope)"
                            >
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column
                label="添加时间"
                prop="created_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="修改时间"
                prop="updated_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="操作"
                fixed="right"
                width="200"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);">编辑</el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteSnmpOidIndexList(scope);">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
       <el-dialog width="50%" :visible.sync="OidAdd" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <OidAdd v-on:freshData="freshData"></OidAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="OidEdit" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <OidEdit v-on:freshData="freshData" v-if="OidEdit" :editId.sync='editId'></OidEdit>
        </el-dialog>
    </div>
</template>

<script>
    import OidAdd from './add/add.vue'
    import OidEdit from './add/edit.vue'
    export default{
        data(){
            return{
                list:[],//表格数据
                tableHeight:600, // 表格高度
                OidAdd:false,
                OidEdit:false,
                editId:'',
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableLoading:false, //表格的加载中
                editId:'', //修改的id
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            getSnmpOidIndexList(){
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                }
                capis.getSnmpOidIndexList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.getSnmpOidIndexList();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    this.getSnmpOidIndexList();
                }
                this.OidAdd = false;
                this.OidEdit = false;
            },
            sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.getSnmpOidIndexList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.getSnmpOidIndexList();
            },
            deleteSnmpOidIndexList(data)
            {
                // 删除
                    this.$confirm('确定删除【'+data.row.serverName+'】?', '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                            id:data.row.id
                        }
                        capis.deleteSnmpOidIndexList(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //去除删掉的数据
                                this.list.splice(data.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
            //状态修改
            changeStatus(data)
            {
                console.log(12312,data)
                this.formLoading=true;
                    //请求接口
                    var params = {
                        act:'changeStatus',
                        id:data.row.id,
                        status:data.row.status
                    }
                capis.saveSnmpOidIndexList(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.$notify({
                            title: '提示',
                            message: '操作成功',
                            type: 'success'
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            editInfo(data)
            {
                this.editId = data.row.id;
                this.OidEdit = true ;
            },
        },
        created(){
            //获取到服务器列表
            this.getSnmpOidIndexList();
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            });
        },
        components:{
            OidAdd,
            OidEdit
        },
    }
</script>

<style scoped lang="scss">
    .devices{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
        .dedevice-run{
            display: flex;
            align-items: center;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>



