 * @Descripttion: 服务列表添加
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 09:52:46
-->
<template>
    <div class="warning-add">
        <p class="warning-add-title">基础选项</p>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
             <el-form-item label="名　　称：" prop="name">
                 <el-input v-model="form.name" clearable></el-input>
             </el-form-item>
             <el-form-item label="标　　识：" prop="key">
                 <el-input v-model="form.key" clearable></el-input>
             </el-form-item>
             <el-form-item>
                 <el-button type="primary" @click="submitForm('form')" :loading="formLoading">增加</el-button>
                 <el-button @click="cancel">取消</el-button>
             </el-form-item>
         </el-form>
    </div>
 </template>
 <script>
     export default{
         data(){
            return{
                form:{
                    name:'',
                    key:'',
                },
                rules: {
                    name: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                    host: [
                        { required: true, message: '请输入表示', trigger: 'blur' },
                    ],
                },
                formLoading:false,
            }
        },
        components:{
        },
        created(){
            this.getRoleIndexList();
        },
        methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.formLoading=true;
                        //请求接口
                        var params = {
                            name:this.form.name,
                            key:this.form.key,
                            id:this.form.id,
                        }
                        capis.saveRoleIndexList(params,{})
                        .then(data=>{
                            this.formLoading = false;
                            if(data.status == 'success'){
                                //对数据进行赋值
                                // 修改父组件的值
                                this.$emit('freshData',{
                                    type:'add',
                                })
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.formLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                        
                    }
                });
            },
            cancel(){
                
               this.$emit('freshData',{
                    type:'cancel',
                })
          },
        }
    }
 </script>
 <style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
        .warning-add-title{
            font-size: 28px;
            font-weight: 600;
            height: 60px;
            line-height: 60px;
        }
        .el-select{
            display: block;
        }
    }
 </style>


