<template>
    <div class="services-list">
        <div class="service-item">
            <div class="service-title">
                <p class="service-border"></p>
                <p class="service-tit">服务器</p>
            </div>
            <div class="service-content" v-if="list">
                <el-card class="box-card" shadow="hover" v-for="(item,index) in list" :key="index" :body-style="{height:'100%','box-sizing': 'border-box'}">
                    <div class="box-info" @click="routeToDetail(item.id,item.running)">
                        <div class="header-info">
                            <div class="hostIcon"><i class="el-icon-office-building"></i></div>
                            <ul>
                                <li>{{item.host}}</li>
                                <li><span v-if="item.system_version">{{item.system_version}}</span><span v-else>-</span></li>
                                <li><span v-if="item.cpu_core_num && item.memory_total">{{item.cpu_core_num}}核 {{item.memory_total.size}}{{item.memory_total.format}}</span><span v-else>-</span></li>
                                <li><i class="el-icon-s-platform status-dot" :style="'color:'+(item.running?'#67C23A':'#888888')"> {{item.running?'在线':'离线'}}</i></li>
                            </ul>
                        </div>
                        <div class="body-info">
                            <div class="item no-margin">
                                <el-tooltip placement="top">
                                    <span slot="content">数据更新时间：{{item.time!='-'?item.time:"-"}}</span>
                                    <span class="time-ago"><timeago v-if="item.time!='-'" :datetime="item.time" :auto-update="60"></timeago><span v-else>-</span></span>
                                </el-tooltip>
                            </div>
                            <div class="item">
                                <!-- <div class="hostName">测试运维系统主机名称超出方块大小场景使用</div> -->
                                
                                <el-tooltip placement="top">
                                    <span slot="content">{{item.name}}</span>
                                    <span class="hostName">{{item.name}}</span>
                                </el-tooltip>
                                
                            </div>
                            <div class="item nowrap"><div>CPU</div>
                                <el-tooltip placement="top">
                                    <span slot="content">cpu使用率：{{item.cpuUse?parseFloat(item.cpuUse)+"%":"-"}}</span>
                                    <div class="cont"><el-progress :percentage="parseFloat(item.cpuUse)" :color="getProgressColor(item.cpuUse)"></el-progress></div>
                                </el-tooltip>
                            </div>
                            <div class="item nowrap"><div>内存</div>
                                <el-tooltip placement="top">
                                    <span slot="content">内存使用率：{{item.memUsedPercent?parseFloat(item.memUsedPercent)+"%":"-"}}</span>
                                    <div class="cont"><el-progress :percentage="parseFloat(item.memUsedPercent)" :color="getProgressColor(item.memUsedPercent)"></el-progress>
                                    </div>
                                </el-tooltip>
                            </div>
                            <div class="item"><div>负载</div>
                                <el-tooltip placement="top">
                                    <span slot="content">1分钟负载：{{item.cpuLoad1}}<br/>5分钟负载：{{item.cpuLoad5}}<br/>15分钟负载：{{item.cpuLoad15}}</span>
                                    <span class="cont load">
                                        <span :class="getLoadClass(item.cpuLoad1)">{{item.cpuLoad1}}</span><span :class="getLoadClass(item.cpuLoad5)">{{item.cpuLoad5}}</span><span :class="getLoadClass(item.cpuLoad15)">{{item.cpuLoad15}}</span>
                                    </span>
                                </el-tooltip>
                            </div>
                            <div class="item nowrap"><div>网络</div>
                                <el-tooltip placement="top">
                                    <span slot="content">上行：{{item.netSpeed.out.size}}{{item.netSpeed.out.format}}<br/>下行：{{item.netSpeed.in.size}}{{item.netSpeed.in.format}}</span>
                                    <span class="network">
                                        <span><i class="el-icon-top up"></i> {{item.netSpeed.out.size}} {{item.netSpeed.out.format}}/s</span>
                                        <span><i class="el-icon-bottom down"></i> {{item.netSpeed.in.size}} {{item.netSpeed.in.format}}/s</span>
                                    </span>
                                </el-tooltip>
                            </div>
                            <div class="item"><div>硬盘</div>
                                <el-popover
                                    placement="top-start"
                                    trigger="hover"
                                    v-if="item.storageInfo.length>0"
                                    >
                                    <el-table :data="item.storageInfo" size="mini" stripe>
                                        <el-table-column property="mounted" label="挂载点"></el-table-column>
                                        <el-table-column label="总量">
                                            <template slot-scope="scope">
                                                {{scope.row.total.size}} {{scope.row.total.format}}
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="已用量">
                                            <template slot-scope="scope">
                                                {{scope.row.used.size}} {{scope.row.used.format}}
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="使用率">
                                            <template slot-scope="scope">
                                                {{scope.row.used_percent}} %
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                    <span class="disk"  slot="reference">
                                        <i v-if="!item.storageWarning" class="el-icon-success succ"> 正常</i><i v-else class="el-icon-warning dang"> 告警</i>
                                    </span>
                                </el-popover>
                                <el-tooltip v-else>
                                    <span slot="content">未获取到硬盘数据</span>
                                    <span class="disk">
                                        <i class="el-icon-info basis"> 未获取到数据</i>
                                    </span>
                                </el-tooltip>
                            </div>
                        </div>
                    </div>
                    <div class="itemServe" v-if="item.role">
                        <div v-for="(it,ind) in item.role" :key="ind" class="item">
                            <el-tag v-if="it.name == '防火墙服务'" size="small"  @click.native="IptableOpen(item.id,it.type,it.name,item.name,item.host)" :type="it.running == 1?'success':'info'" class="service">
                            {{it.name}}
                            </el-tag>
                            <el-tag v-else size="small" @click.native="ServicesOpen(item.id,it.type,it.name,item.name,item.host)" :type="it.running == 1?'success':'info'" class="service">
                            {{it.name}}
                            </el-tag>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                list:[],
                progressColors:{
                    'success': '#67C23A',
                    'warning': '#E6A23C',
                    'danger': '#F56C6C',
                },
            }
        },
        methods:{
            getEnableServices(){
                var params = {}
                    capis.getEnableServices(params,{})
                    .then(data=>{
                        this.tableLoading = false;
                        if(data.status == 'success'){
                            //对数据进行赋值
                            for( let i=0; i< data.res.data.length;i++)
                            {
                                let row = data.res.data[i];
                                row.time = row.time>0?ctool.getYMDHMS(row.time):"-";
                                this.list.push(row)
                            }
                            this.list = data.res.data;
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    })
                    .catch(err=>{
                        this.tableLoading = false;
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            routeToDetail(id,running)
            {
                console.log(id,running);
                if(running)
                {
                    this.$router.push({path:'/index/services/servicesinfo',query:{hostId:id}})
                }
                else
                {
                    this.$notify({
                        title: '提示',
                        message: "离线主机不可查看详情",
                        type: 'warning'
                    });
                }
            },
            ServicesOpen(id,type,name,deviceName,deviceHost)
            {
                this.$router.push({path:'/index/warninfo/errorlog',query:{hostId:id,type:type,name:name,deviceName:deviceName,deviceHost:deviceHost}})
            },
            IptableOpen(id,type,name,deviceName,deviceHost)
            {
                this.$router.push({path:'/index/iptableInfo/ip',query:{hostId:id,type:type,name:name,deviceName:deviceName,deviceHost:deviceHost}})
            },
            initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('Snmp.HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.list.length>0)
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].running = e.data.running;
                                    this.list[index].time = e.time>0?ctool.getYMDHMS(e.time):"-";
                                }
                            });
                        }
                    }
                    else if(e.type=="HostAdd" || e.type=="HostDel" || e.type=="HostChange")
                    {
                        this.getEnableServices();
                    }
                })
                this.$LaravelEcho.instance.channel('SysInfo').listen('Snmp.SysInfoEvent', (e) => {
                    if(this.list.length>0)
                    {
                        //检测cpu的使用率
                        if(e.type=="CpuInfo")
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].cpuUse = e.data.cpuUse;
                                    this.list[index].cpuLoad1 = e.data.cpuLoad1;
                                    this.list[index].cpuLoad5 = e.data.cpuLoad5;
                                    this.list[index].cpuLoad15 = e.data.cpuLoad15;
                                    this.list[index].time = e.time>0?ctool.getYMDHMS(e.time):"-";
                                }
                            });
                        }
                        //内存的使用率
                        else if(e.type =='MemoryInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].memUsedPercent = e.data.memUsedPercent;
                                    this.list[index].time = e.time>0?ctool.getYMDHMS(e.time):"-";
                                }
                            });
                        }
                        //网络
                        else if(e.type =='ifInfo')
                        {
                            console.log(e)
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].netSpeed.in.size = e.data.escape.in.size; //下行量
                                    this.list[index].netSpeed.in.format = e.data.escape.in.format; //下行单位
                                    this.list[index].netSpeed.out.size = e.data.escape.out.size; //上行量
                                    this.list[index].netSpeed.out.format = e.data.escape.out.format; //上行单位
                                    this.list[index].time = e.time>0?ctool.getYMDHMS(e.time):"-";
                                }
                            });
                        }
                        // 硬盘
                        else if(e.type == 'StorageInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].storageInfo = e.data;
                                    this.list[index].time = e.time>0?ctool.getYMDHMS(e.time):"-";
                                }
                            });
                        }
                    }
                })
            },
            getProgressColor(percentage) {
                if(percentage <= 70) {
                    return this.progressColors.success
                }
                else if(percentage > 70 && percentage <= 90) {
                    return this.progressColors.warning
                }
                else{
                    return this.progressColors.danger
                }
            },
            getLoadClass(load)
            {
                if(load <= 5) {
                    return 'succ'
                }
                else if(load > 5 && load <= 10) {
                    return 'warn'
                }
                else{
                    return 'dang'
                }
            }
        },
        created(){
            this.getEnableServices();
            this.$nextTick(()=>{
                this.initWs();
            })
        },
        beforeDestroy(){
            this.$LaravelEcho.instance.leaveChannel('SysInfo');
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        }
    }
</script>

<style scoped lang="scss">
    .services-list{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        .service-item .service-title{
            display: flex;
            font-size: 20px;
            font-weight: 600;
            .service-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
            }
            .service-tit{
                margin-left: 20px;
            }
        }
        .service-item .service-content {
            padding: 24px;
            display: flex;
            align-items: stretch;
            flex-wrap: wrap;
            /*当页面宽度大于1000px且小于1200px的时候执行，1000-1200*/
            @media screen and (min-width:1000px) and (max-width: 1280px){
                .box-card {
                    width: calc(100% - 10px );
                }
                .box-card .box-info .header-info {
                    width: 20%;
                }
                .box-card .box-info .body-info {
                    width: 100%;
                }
            }
            /*当页面宽度大于1280px且小于1366px的时候执行,1280-1366*/
            @media screen and (min-width:1281px) and (max-width: 1439px){
                .box-card {
                    width: calc(50% - 10px );
                }
                .box-card .box-info .header-info {
                    width: 20%;
                }
            }
            /*当页面宽度大于1440px且小于1600px的时候执行,1440-1600*/
            @media screen and (min-width:1440px) and (max-width:1679px){
                .box-card {
                    width: calc(50% - 10px );
                }
                .box-card .box-info .header-info {
                    width: 20%;
                }
            }
            /*当页面宽度大于1680px且小于1920px的时候执行,1680-1920*/
            @media screen and (min-width:1680px) and (max-width:1919px){
                .box-card {
                    width: calc(33.333333% - 10px );
                }
                .box-card .box-info .header-info {
                    width: 20%;
                }
            }
            @media screen and (min-width:1920px) and (max-width:2047px){
                .box-card {
                    width: calc(25% - 10px );
                }
                .box-card .box-info .header-info {
                    width: 75px;
                }
            }
            @media screen and (min-width:2048px) and (max-width:3439px){
                .box-card {
                    width: 420px;
                }
                .box-card .box-info .header-info {
                    width: 75px;
                }
            }
            @media screen and (min-width:3440px) and (max-width:4095px){
                .box-card {
                    width: 495px;
                }
                .box-card .box-info .header-info {
                    width: 75px;
                }
                .box-card .box-info .body-info {
                    width: calc(80% - 10px);
                }
            }
            @media screen and (min-width:4096px){
                .box-card {
                    width: 420px;
                }
                .box-card .box-info .header-info {
                    width: 75px;
                }
            }

            
            .box-card {
                margin: 5px;
                display: inline-block;
                box-sizing: border-box;
                .box-info {
                    display: flex;
                    align-items: stretch;
                    box-sizing: border-box;
                    font-size: 12px;
                    .header-info {
                        text-align: center;
                    }
                    .header-info .hostIcon {
                        font-size: 50px;
                    }
                    .header-info ul {
                        list-style: none;
                        padding: 0;
                        margin: 0;
                    }
                    .header-info ul li {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-top: 5px;
                    }
                    .status-dot{
                        padding-top: 10px;
                    }

                    .body-info {
                        border-left: 1px solid #F6F6F6;
                        margin: 0 0 0 10px;
                        padding: 0 0 0 10px;
                        width: calc(100% - 100px);
                    }
                    .body-info .hostName {
                        margin-bottom: 10px;
                        font-size: 16px;
                        font-weight: bold;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        flex: 1;
                        white-space: nowrap;
                    }
                    .body-info .item.no-margin {
                        margin-top: 0;
                    }
                    .body-info .item {
                        display: flex;
                        align-items: center;
                        margin-top: 8px;
                    }
                    .body-info .item.nowrap {
                        white-space: nowrap;
                    }
                    .body-info .item .cont {
                        flex-grow: 1;
                        margin-left: 10px;
                    }
                    .body-info .item .cont.load span {
                        padding: 0 10px;
                        border-left: 1px solid #EEE;
                        white-space: nowrap;
                    }
                    .body-info .item .cont.load span:first-child {
                        border-left: none;
                    }
                    .body-info .item .cont.load .succ {
                        color: #909399,
                    }
                    .body-info .item .cont.load .warn {
                        color: #E6A23C,
                    }
                    .body-info .item .cont.load .dang {
                        color: #F56C6C,
                    }
                    .body-info .item .network {
                        margin-left: 10px;
                    }
                    .body-info .item .network .up {
                        color:#E6A23C
                    }
                    .body-info .item .network .down {
                        color:#409EFF
                    }
                    .body-info .time-ago {
                        color:#888888;
                        margin-left: auto;
                    }
                    .body-info .disk {
                        margin-left: 10px;
                    }
                    .body-info .item .disk .basis {
                        color: #C0C4CC,
                    }
                    .body-info .item .disk .succ {
                        color: #67C23A,
                    }
                    .body-info .item .disk .warn {
                        color: #E6A23C,
                    }
                    .body-info .item .disk .dang {
                        color: #F56C6C,
                    }
                }
                .itemServe {
                    border-top:1px solid #EEEEEE;
                    padding-top: 15px;
                    margin-top:15px;
                }
                .itemServe .item {
                    display:inline-block
                }
                .itemServe .item .service {
                    margin-right:5px;
                    margin-bottom: 5px;
                }
            }

        }
    }
</style>


