<!--
 * @Descripttion: 设备管理添加
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 09:52:46
-->
<template>
    <div class="warning-add">
        <p class="warning-add-title">基础选项</p>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
            <el-form-item label="服务名称：" prop="serverName">
                <el-input v-model="form.serverName" clearable></el-input>
            </el-form-item>
            <el-form-item label="监控名称：" prop="serverType">
                <el-input v-model="form.serverType" clearable></el-input>
            </el-form-item>
            <el-form-item label="Oid：" prop="oid">
                <el-input v-model="form.oid" clearable></el-input>
            </el-form-item>
            <el-form-item label="类型：" prop="type">
                <el-select v-model="form.type" placeholder="请选择" clearable>
                    <el-option
                        label="系统自带"
                        value="1">
                    </el-option>
                    <el-option
                        label="用户安装"
                        value="2">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="状　　态：" prop="status">
                <el-switch
                    v-model="form.status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    :active-value='1'
                    :inactive-value='2'
                    >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')" :loading="formLoading">增加</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
 </template>
<script>
    export default{
        props:{
            editId:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    serverName:'',
                    serverType:'',
                    oid:'',
                    type:'', // 类型
                    status:1
                },
                rules: {
                    serverName: [
                        { required: true, message: '请输入服务名称', trigger: 'blur' },
                    ],
                    serverType: [
                        { required: true, message: '请输入监控名称', trigger: 'blur' },
                    ],
                    oid: [
                        { required: true, message: '请输入Oid', trigger: 'blur' },
                    ],
                    type: [
                        { required: true, message: '请选择类型', trigger: 'blur' },
                    ],
                },
                roleList:[], // 服务器角色的数据
                formLoading:false,
                NotificationSettingIdArr:null, //告警规则
            }
        },
        components:{
        },
        created(){
            this.getSnmpOidIndexList();
            this.SnmpOidIndexListInfo();
        },
        methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.formLoading=true;
                        //请求接口
                        var params = {
                            serverName:this.form.serverName,
                            serverType:this.form.serverType,
                            oid:this.form.oid,
                            id:this.editId,
                            type:this.form.type,
                            status:this.form.status
                        }
                        capis.saveSnmpOidIndexList(params,{})
                        .then(data=>{
                            this.formLoading = false;
                            if(data.status == 'success'){
                                //对数据进行赋值
                                // 修改父组件的值
                                this.$emit('freshData',{
                                    type:'add',
                                })
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.formLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                        
                    }
                });
            },
            getSnmpOidIndexList(){
                var params = {};
                capis.getSnmpOidIndexList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.roleList = data.res;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            SnmpOidIndexListInfo(){
                var params = {
                    id:this.editId
                };
                    capis.SnmpOidIndexListInfo(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            //对数据进行赋值
                            this.form.serverName = data.res.data.serverName;
                            this.form.serverType = data.res.data.serverType;
                            this.form.oid = data.res.data.oid;
                            this.form.type = data.res.data.type;
                            this.form.status = data.res.data.status;
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            cancel(){
                    this.$emit('freshData',{
                        type:'cancel',
                    })
            },
        }
    }
</script>
<style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
        .warning-add-title{
            font-size: 28px;
            font-weight: 600;
            height: 60px;
            line-height: 60px;
        }
        .el-select{
            display: block;
        }
    }
</style>


