<!--
 * @Descripttion: 端口规则
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:31
-->
<template>
    <div class="devices">
        <div>
            <div class="xinzeng">
                <el-button icon="el-icon-circle-plus-outline" type="success" @click="serviceShow = !serviceShow">新增</el-button>
            </div>
            <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            :height="tableHeight"
            stripe
            highlight-current-row
            v-loading="tableLoading"
            >
                <el-table-column
                label="源地址"
                prop="address"
                sortable
                >
                </el-table-column>
                <el-table-column
                label="类型"
                prop="types"
                sortable
                >
                    <template slot-scope="scope">
                       <div class="dedevice-types">
                           <p style="width:12px;height:12px;border-radius:50%;margin-right:5px;" :style="{backgroundColor:(scope.row.types ==1 ?'#05f360':'red')}"></p>
                           <p v-if="scope.row.types == 1">放行</p>
                           <p v-if="scope.row.types == 2">禁止</p>
                       </div>
                    </template>
                </el-table-column>
                <el-table-column
                label="端口"
                prop="ports"
                sortable
                >
                </el-table-column>
                <el-table-column
                label="协议"
                prop="protocol"
                sortable
                >
                    <template slot-scope="scope">
                       <div class="dedevice-types">
                           <p v-if="scope.row.protocol == 1">tcp</p>
                           <p v-if="scope.row.protocol == 2">udp</p>
                       </div>
                    </template>
                </el-table-column>
                <el-table-column
                label="设置时间"
                prop="addtime"
                sortable
                >
                </el-table-column>
                <el-table-column
                prop="brief"
                label="备注"
                sortable
                >
                </el-table-column>
                <el-table-column
                label="操作"
                fixed="right"
                width="200"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);">编辑</el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteIptable(scope);">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
           <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
        <el-dialog width="50%" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <IptableAdd v-on:freshData="freshData" :hostId="hostId"></IptableAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="serviceEditShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <IptableEdit v-on:freshData="freshData" v-if="serviceEditShow" :editId.sync='editId' :form = "editObject"></IptableEdit>
        </el-dialog>
    </div>
 </template>
 <script>
    import IptableAdd from './add/add.vue'
    import IptableEdit from './add/edit.vue'
     export default{
         data(){
             return{
                list:[],//表格数据
                tableHeight:600, // 表格高度
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                hostId:'',
                type:'',
                editId:'',
                tableLoading:false, //表格的加载中, 
                serviceShow:false,
                serviceEditShow:false,
                editObject:{},
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-40
            },
            hostseverpyinfo(){
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                    uri:'/iptables/get_rules_list',
                    hostId:this.hostId,
                }
                capis.hostseverpyinfo(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.hostseverpyinfo();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    this.hostseverpyinfo();
                }
                this.serviceShow = false;
                this.serviceEditShow = false;
            },
            sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.hostseverpyinfo();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.hostseverpyinfo();
            },
            editInfo(data)
            {
                //编辑
                this.editObject = data.row;
                this.editObject["hostId"] = this.hostId;
                this.serviceEditShow = true;
            },
            deleteIptable(data)
            {
                // 删除
                 this.$confirm('确定删除【'+data.row.source+'】?', '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                        uri:'/iptables/remove_rules',
                        id:data.row.id,
                        hostId:this.hostId,
                        protocol:data.row.protocol,
                        ports:data.row.ports,
                        types:data.row.types,
                        brief:data.row.brief,
                        source:data.row.source,
                        }
                        capis.hostseverpyinfo(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //去除删掉的数据
                                this.list.splice(data.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
        },
        mounted(){
        },
        components:{
            IptableAdd,
            IptableEdit
        },
        created(){
            this.$nextTick(()=>{
                this.getHeight();
            })
            //获取到列表
            this.hostId = this.$route.query.hostId;
            this.type = this.$route.query.type;
            this.hostseverpyinfo();
        },
        computed:{
            
        }
     }
 </script>
 
 <style scoped lang="scss">
    .devices{
        .message-pagenation{
            text-align: center;
            margin-top: 20px;
        }
        .xinzeng{
            margin-top: 10px;
            float:left;
            margin-bottom: 5px;
        }
        .dedevice-types{
            display: flex;
            align-items: center;
        }
    }
 </style>


